import {searchSkill, searchSkillConfigs} from "@/utils/SourceDataUtil";
import {ConvertSkillToAttrGroup, SkillAndConfig} from "@/model/grade/grade-skill";

export default {

    calcSkillGrade(skillId) {
        const skill = searchSkill(skillId);
        const skillConfigs = searchSkillConfigs(skillId) || [];
        const sc = new SkillAndConfig(skillConfigs, skill['attr_selfRange']);
        const ag = ConvertSkillToAttrGroup(sc)
        const g = ag.calcGrade()
        return g
    }
}