import Vue from "vue";
import Vuex from 'vuex'
import {requestData} from "@/utils/DataRequestUtil";
import {defaultVersion} from "@/config/version-config";

Vue.use(Vuex);

function createSuitIndex(SuitSource) {
    const suitIndex = {};
    Object.values(SuitSource).forEach(suit => {
        suitIndex[suit.suitKey] = suit;
    });
    return suitIndex;
}

function createSkillConfigIndex(SkillConfigSource) {
    const skillConfigIndex = {};
    SkillConfigSource.forEach(skillConfig => {
        if (!skillConfig[skillConfig['skillId']]) {
            skillConfigIndex[skillConfig['skillId']] = [];
        }
        skillConfigIndex[skillConfig['skillId']].push(skillConfig);
    });
    return skillConfigIndex;
}

function createTitleIndex(TitleSource) {
    const titleIndex = {};
    TitleSource.forEach(title => {
        titleIndex[title.skillId] = title;
    });
    return titleIndex;
}

export default new Vuex.Store({
    state: {
        CurVersion: defaultVersion,

        HeroSource: {},
        ItemSource: {},
        SkillSource: {},
        SuitSource: {},
        SkillConfigSource: [],
        TitleConfigSource: [],
        EventSource: {},
        Const: {},

        SuitIndex: {},
        SkillConfigIndex: {},
        TitleIndex: {},
    },
    getters: {
    },
    mutations: {
        setVersion(state, version) {
            console.log('setVersion', version);
            state.CurVersion = version;
        },
        setHeroSource(state, data) {
            state.HeroSource = data;
        },
        setItemSource(state, data) {
            state.ItemSource = data
        },
        setSkillSource(state, data) {
            state.SkillSource = data
        },
        setSuitSource(state, data) {
            state.SuitSource = data
            state.SuitIndex = createSuitIndex(data)
        },
        setSkillConfigSource(state, data) {
            state.SkillConfigSource = data
            state.SkillConfigIndex = createSkillConfigIndex(data)
        },
        setTitleConfigSource(state, data) {
            state.TitleConfigSource = data
            state.TitleIndex = createTitleIndex(data)
        },
        setEventSource(state, data) {
            state.EventSource = data
        },
        setConst(state, data) {
            state.Const = data
        }
    },
    actions: {
        initStore({commit, state}) {
            return Promise.all([
                requestData('hero', state.CurVersion).then(data => commit('setHeroSource', data)),
                requestData("item", state.CurVersion).then(res => commit("setItemSource", res)),
                requestData("skill", state.CurVersion).then(res => commit("setSkillSource", res)),
                requestData("suit", state.CurVersion).then(res => commit("setSuitSource", res)),
                requestData("skill_config", state.CurVersion).then(res => commit("setSkillConfigSource", res)),
                requestData("title_config", state.CurVersion).then(res => commit("setTitleConfigSource", res)),
                requestData("event", state.CurVersion).then(res => commit("setEventSource", res)),
                requestData("const", state.CurVersion).then(res => commit("setConst", res)),
            ])
        }
    }
})