export class AttrEntries {
    constructor() {
        this.valueAttrs = {}
        this.classValueAttrs = {}
        this.flags = {}
        this.immunes = {}
        this.deBuffMakes = {}
    }

    // functions
    getValue(attrType) {
        return this.valueAttrs[attrType] || 0
    }
    setValue(attrType, value) {
        this.valueAttrs[attrType] = value
    }
    getValueInc(attrType) {
        const val = this.getValue(attrType)
        return val != 0 ? val - 1 : 0
    }
    getClassValue(clsType, attrType) {
        return this.classValueAttrs[clsType] && this.classValueAttrs[clsType][attrType] || 0
    }
    setClassValue(clsType, attrType, value) {
        if (!this.classValueAttrs[clsType]) {
            this.classValueAttrs[clsType] = {}
        }
        this.classValueAttrs[clsType][attrType] = value
    }
    getClassValueInc(clsType, attrType) {
        const val = this.getClassValue(clsType, attrType)
        return val != 0 ? val - 1 : 0
    }
    getClassValues(attrType) {
        return classes.map(cls => this.getClassValue(cls, attrType))
    }
    getClassValuesInc(attrType) {
        return classes.map(cls => this.getClassValueInc(cls, attrType))
    }
    addDeBuffMake(type, rate, round) {
        if (!this.deBuffMakes[type]) {
            this.deBuffMakes[type] = []
        }
        this.deBuffMakes[type].push({ rate, round })
    }
    addImmune(type) {
        this.immunes[type] = true
    }
}

export default {
    constants,
    castConf,
    rangeConf,
    debuffConf,
    immuneConf,
    classes,
    immuneTags,
    debuffTags,
    getTotalAtk(ae) {
        return ae.getValueInc('裸攻') * constants['标准裸攻'] +
            ae.getValueInc('场攻') * constants['标准场攻']
    },

    getPower(ae) {
        return ae.getValueInc('威力') * constants['标准武功威力']
    },

    getDePower(ae) {
        return -ae.getValueInc('减威') * constants['标准武功威力']
    },

    getClassPower(ae) {
        return classes.map(cls => ae.getClassValueInc(cls, '威力') * constants['标准武功威力'])
    },

    getClassDePower(ae) {
        return classes.map(cls => -ae.getClassValueInc(cls, '减威') * constants['标准武功威力'])
    },

    getTotalDef(ae) {
        return ae.getValueInc('裸防') * constants['标准裸防'] +
            ae.getValueInc('场防') * constants['标准场防']
    },

    getTotalAgi(ae) {
        return ae.getValueInc('裸轻') * constants['标准裸轻'] +
            ae.getValueInc('场轻') * constants['标准场轻']
    }
}