import gc from "@/model/grade/grade-config";

export class GradeEntry {
    constructor(type, pairs) {
        this.type = type
        this.values = {}
        if (pairs) {
            for (let pair of pairs) {
                this.values[pair[0]] = pair[1]
            }
        }
        this.gradePoint = 0.0
    }

    toString() {
        return `${this.type}: ${this.gradePoint.toFixed(2)}`
    }

    detail() {
        return `${this.type}: ${this.gradePoint.toFixed(2)}, detail: ${JSON.stringify(this.values)}`
    }
}

export class GradeEntries {
    constructor(factor) {
        this.entries = []
        this.factor = factor || 1.0
    }

    addEntry(entry) {
        if (entry && entry.gradePoint > 0) {
            this.entries.push(entry)
        }
    }

    calcTotalGradePoint() {
        let total = 0.0
        this.entries.forEach(entry => {
            total += entry.gradePoint
        })
        this.totalGrade = total
        return total * this.factor
    }

    toString() {
        const str = this.entries.map(entry => entry.toString()).join(',')
        return `总分：${this.calcTotalGradePoint().toFixed(2)}， 系数：${this.factor}, 明细：[${str}]`
    }

    detail() {
        const str = this.entries.map(entry => entry.detail()).join('\n')
        return `总分：${this.calcTotalGradePoint().toFixed(2)}， 系数：${this.factor}\n详情：\n${str}`
    }
}

export function convertAttrToGrade(ae, factor) {
    let ges = new GradeEntries(factor)
    ges.addEntry(speedGradeEntry(ae))
    ges.addEntry(atkGradeEntry(ae))
    ges.addEntry(defGradeEntry(ae))
    ges.addEntry(atkSoftGradeEntry(ae))
    ges.addEntry(defSoftGradeEntry(ae))
    ges.addEntry(critGradeEntry(ae))
    ges.addEntry(doubleHitGradeEntry(ae))
    ges.addEntry(castGradeEntry(ae))
    ges.addEntry(rangeGradeEntry(ae))
    ges.addEntry(suckGradeEntry(ae))
    ges.addEntry(injuryGradeEntry(ae))
    ges.addEntry(counterGradeEntry(ae))
    ges.addEntry(debuffGradeEntry(ae))
    ges.addEntry(immuneGradeEntry(ae))
    return ges
}

function calcClassesAtks(ae) {
    const atk = gc.getTotalAtk(ae)
    const power = gc.getPower(ae)
    const spePower = gc.getClassPower(ae)
    const enemyDefDec = gc.constants['标准敌方场防'] * ae.getValueInc('敌防')
    const atks = gc.classes.map((value, index) => {
        return atk + power + spePower[index] - enemyDefDec * 2
    })
    return atks
}

function calcAtkAndFactorPair(atkValues) {
    const mainFactor = gc.constants['主系分值权重']
    const viceFactor = (1 - mainFactor) / 4
    let pairs = []
    atkValues.forEach((value) => {
        pairs.push({value: value, factor: viceFactor})
    })
    pairs.sort((a, b) => {
        return b.value - a.value
    })
    pairs[0].factor = mainFactor
    return pairs;
}

function atkGradeEntry(ae) {
    const atks = calcClassesAtks(ae)
    const ge = new GradeEntry('硬攻分', [['各系攻击', atks]])

    // 总分 = 主系分 * 0.9 + 其他四系分 * 0.025
    const expFactor = gc.constants['破防指数系数']
    const gradeFactor = gc.constants['破防分值系数']
    let pairs = calcAtkAndFactorPair(atks);
    let gp = 0.0
    pairs.forEach((pair) => {
        gp += calcPowerGrade(pair.value, expFactor, gradeFactor) * pair.factor
    })
    ge.gradePoint = gp
    return ge
}

function calcClassesDefs(ae) {
    const def = gc.getTotalDef(ae)
    const dePower = gc.getDePower(ae)
    const speDePower = gc.getClassDePower(ae)
    const enemyAtkDec = gc.constants['标准敌方场攻'] * ae.getValueInc('敌攻')
    const defs = gc.classes.map((value, index) => {
        return def * 2 + dePower + speDePower[index] - enemyAtkDec
    })
    return defs
}

function defGradeEntry(ae) {
    const defs = calcClassesDefs(ae)
    const ge = new GradeEntry('硬防分', [['各系防御', defs]])

    // 总分 = 各系分之和 * 0.2
    const expFactor = gc.constants['减破防指数系数']
    const gradeFactor = gc.constants['破防分值系数']
    let gp = 0.0
    defs.forEach((value) => {
        gp += calcPowerGrade(value, expFactor, gradeFactor) * 0.2
    })
    ge.gradePoint = gp
    return ge
}

function critGradeEntry(ae) {
    const critRate = ae.getValue('暴击')
    if (critRate == 0) {
        return null
    }
    const atks = calcClassesAtks(ae)
    const ge = new GradeEntry('暴击分', [['各系攻击', atks], ['暴击率', critRate]])

    const critPower = gc.constants['标准暴击威力']
    const expFactor = gc.constants['破防指数系数']
    const gradeFactor = gc.constants['破防分值系数']
    let pairs = calcAtkAndFactorPair(atks);
    let gp = 0.0
    pairs.forEach((pair) => {
        const gpCrit = calcPowerGrade(pair.value + critPower, expFactor, gradeFactor)
        const gpNoCrit = calcPowerGrade(pair.value, expFactor, gradeFactor)
        gp += (gpCrit - gpNoCrit) * critRate * pair.factor
    })
    ge.gradePoint = gp
    return ge
}

function speedGradeEntry(ae) {
    const atk = gc.getTotalAtk(ae)
    const def = gc.getTotalDef(ae)
    const agi = gc.getTotalAgi(ae)
    const enemyAtkDec = gc.constants['标准敌方场攻'] * ae.getValueInc('敌攻')
    const enemyDefDec = gc.constants['标准敌方场防'] * ae.getValueInc('敌防')
    const enemyAgiDec = gc.constants['标准敌方场轻'] * ae.getValueInc('敌轻')
    const asp = ae.getValueInc('时序')
    const enemyAsp = ae.getValueInc('敌时')
    const ge = new GradeEntry('时序分', [
        ['攻防轻', [atk, def, agi]], ['时序', asp], ['敌方攻防轻', [enemyAtkDec, enemyDefDec, enemyAgiDec]], ['敌方时序', enemyAsp]])

    const baseSpeed = gc.constants['标准时序']
    const enemyBaseSpeed = gc.constants['标准敌方时序']
    const speedFactor = gc.constants['时序分值系数']
    const esp = enemyAtkDec + enemyDefDec + enemyAgiDec * 2
    const esr = (enemyBaseSpeed + esp) * (1 + enemyAsp) / enemyBaseSpeed
    const totalSpeedRate = asp + (atk + def + agi * 2) * (1 + asp) / baseSpeed + 1 / esr - 1
    const gp = totalSpeedRate * speedFactor
    ge.gradePoint = gp
    return ge
}

function atkSoftGradeEntry(ae) {
    const agi = gc.getTotalAgi(ae)
    const hitInc = ae.getValueInc('命中')
    const dmgInc = ae.getValueInc('伤害')
    const enemyAgiDec = gc.constants['标准敌方场轻'] * ae.getValueInc('敌轻')
    const classHitIncs = ae.getClassValuesInc('命中')
    const classDmgIncs = ae.getClassValuesInc('伤害')
    const speedHitStandard = gc.constants['轻功命中基准值']
    const atkValues = []
    for (let i = 0; i < gc.classes.length; i++) {
        classHitIncs[i] = (1 + hitInc) * (1 + classHitIncs[i]) * (1 + (agi - enemyAgiDec) / speedHitStandard)
        classDmgIncs[i] = (1 + dmgInc) * (1 + classDmgIncs[i])
        atkValues.push(classHitIncs[i] * classDmgIncs[i])
    }
    const ge = new GradeEntry('软攻分', [['各系命中', classHitIncs], ['各系伤害', classDmgIncs]])

    const pairs = calcAtkAndFactorPair(atkValues);
    let gp = 0.0
    const gradeFactor = gc.constants['命中伤害分值系数']
    pairs.forEach((pair) => {
        gp += calcLinearGrade(pair.value - 1, 0, gradeFactor) * pair.factor
    })
    ge.gradePoint = gp
    return ge
}

function defSoftGradeEntry(ae) {
    const agi = gc.getTotalAgi(ae)
    const hitDec = ae.getValueInc('闪避')
    const dmgDec = ae.getValueInc('减伤')
    const enemyAgiDec = gc.constants['标准敌方场轻'] * ae.getValueInc('敌轻')
    const classHitDecs = ae.getClassValuesInc('闪避')
    const classDmgDecs = ae.getClassValuesInc('减伤')
    const speedHitStandard = gc.constants['轻功命中基准值']
    const defValues = []
    for (let i = 0; i < gc.classes.length; i++) {
        classHitDecs[i] = (1 + hitDec) * (1 + classHitDecs[i]) * (1 - (agi - enemyAgiDec) / speedHitStandard)
        classDmgDecs[i] = (1 + dmgDec) * (1 + classDmgDecs[i])
        defValues.push(classHitDecs[i] * classDmgDecs[i])
    }
    const ge = new GradeEntry('软防分', [['各系闪避', classHitDecs], ['各系减伤', classDmgDecs]])
    const gradeFactor = gc.constants['命中伤害分值系数']
    let gp = 0.0
    defValues.forEach((value) => {
        const convertValue = 1.0 / value
        gp += calcLinearGrade(convertValue - 1, 0, gradeFactor) * 0.2
    })
    ge.gradePoint = gp
    return ge
}

function doubleHitGradeEntry(ae) {
    const dh = ae.getValue('双击')
    if (dh == 0) {
        return null
    }
    const ge = new GradeEntry('双击分', [['双击', dh]])

    const dhFactor = gc.constants['双击分值系数']
    const gp = calcLinearGrade(dh, 0, dhFactor)
    ge.gradePoint = gp
    return ge
}

function castGradeEntry(ae) {
    const castRate = ae.getValue('出招')
    const classCastRates = ae.getClassValues('出招')
    const ge = new GradeEntry('出招分', [['全系出招', castRate], ['单系出招', classCastRates]])
    let gp = 0.0
    gp += calcCastGrade(castRate, gc.castConf, conf => conf[2])
    gp += calcCastGrade(Math.max(...classCastRates), gc.castConf, conf => conf[1])
    ge.gradePoint = gp
    return ge
}

function rangeGradeEntry(ae) {
    const move = ae.getValue('移动')
    const atkRange = ae.getValue('射程')
    const aoeRange = ae.getValue('范围')
    const ge = new GradeEntry('距离分', [['移动', move], ['射程', atkRange], ['范围', aoeRange]])

    let gp = 0.0
    gp += calcRangeGrade(move, gc.rangeConf, conf => conf[1])
    gp += calcRangeGrade(atkRange, gc.rangeConf, conf => conf[2])
    gp += calcRangeGrade(aoeRange, gc.rangeConf, conf => conf[3])
    ge.gradePoint = gp
    return ge
}

function suckGradeEntry(ae) {
    const suck = ae.getValue('吸血')
    if (suck == 0) {
        return null
    }
    const ge = new GradeEntry('吸血分', [['吸血', suck]])

    const suckBase = gc.constants['吸血分值基础']
    const suckFactor = gc.constants['吸血分值系数']
    const gp = calcLinearGrade(suck, suckBase, suckFactor)
    ge.gradePoint = gp
    return ge
}

function injuryGradeEntry(ae) {
    const injury = ae.getValueInc('加受伤')
    const injuried = ae.getValueInc('减受伤')
    if (injury == 0 && injuried == 0) {
        return null
    }
    const ge = new GradeEntry('受伤分', [['加受伤', injury], ['减受伤', injuried]])

    const injuryFactor = gc.constants['攻击受伤度分值系数']
    const injuriedFactor = gc.constants['被攻击受伤度分值系数']
    const gp = calcSquareGrade(injury, injuryFactor) + calcSquareGrade(injuried,  injuriedFactor)
    ge.gradePoint = gp
    return ge
}

function counterGradeEntry(ae) {
    const counter = ae.getValue('反弹')
    const reflect = ae.getValue('反震')
    const transfer = ae.getValue('挪移')
    if (counter == 0 && reflect == 0 && transfer == 0) {
        return null
    }
    const ge = new GradeEntry('反击分', [['反弹', counter], ['反震', reflect], ['挪移', transfer]])

    const counterFactor = gc.constants['伤害反弹分值系数']
    const reflectFactor = gc.constants['伤害反震分值系数']
    const transferFactor = gc.constants['伤害转移分值系数']
    const gp = calcLinearGrade(counter, 0, counterFactor)
        + calcLinearGrade(reflect, 0, reflectFactor)
        + calcLinearGrade(transfer, 0, transferFactor)
    ge.gradePoint = gp
    return ge
}

function immuneGradeEntry(ae) {
    if (ae.immunes.length == 0) {
        return null
    }
    let gp = 0.0
    const immunes = []
    Object.entries(gc.immuneConf).forEach(([key, conf]) => {
        if (ae.immunes[key]) {
            gp += conf
            immunes.push(key)
        }
    })
    const ge = new GradeEntry('免疫分', [['免疫', immunes]])
    ge.gradePoint = gp
    return ge
}

function debuffGradeEntry(ae) {
    if (ae.deBuffMakes.length == 0) {
        return null
    }
    let gp = 0
    const debuffs = []
    Object.entries(gc.debuffConf).forEach(([key, conf]) => {
        const dms = ae.deBuffMakes[key]
        if (dms == null) {
            return
        }
        dms.forEach((dm) => {
            const opt = {}
            if (key == '点穴') {
                opt['square'] = true
            }
            gp += calcDebuffGrade(dm.rate/100, dm.round, gc.constants['异常概率基础值'], conf, opt)
            debuffs.push([key, dm.rate, dm.round])
        })
    })
    const ge = new GradeEntry('异常分', [['异常', debuffs]])
    ge.gradePoint = gp
    return ge
}

function calcPowerGrade(power, expFactor, gradeFactor) {
    const gp = power / 100
    return gp * (1 + gp * expFactor) * gradeFactor
}

function calcLinearGrade(value, base, factor) {
    return base + value * factor
}

function calcSquareGrade(value, factor) {
    return value * value * factor
}

function calcCastGrade(value, confs, getter) {
    const intValue = Math.round(value * 100)
    if (intValue <= 0) {
        return 0
    }
    let index = confs.findIndex(conf => intValue < conf[0]) - 1
    if (index < -1) {
        index = confs.length - 1
    }
    return getter(confs[index])
}

function calcRangeGrade(value, confs, getter) {
    const intValue = Math.round(value)
    if (intValue <= 0) {
        return 0
    }
    let index = confs.findIndex(conf => intValue < conf[0]) - 1
    let gp = getter(confs[0])
    let pre = 0.0
    if (index < -1) {
        index = confs.length - 1
    }
    for (let i = 0; i < index; i++) {
        gp += (confs[i][0] - pre) * getter(confs[i])
        pre = confs[i][0]
    }
    gp += (intValue - pre) * getter(confs[index])
    if (gp > getter(confs[0]) * 20) {
        gp = getter(confs[0]) * 20
    }
    return gp
}

function calcDebuffGrade(rate, round, base, factor, option) {
    if (rate == 0 || round == 0) {
        return 0
    }
    let fixedRate = rate + base
    if (fixedRate > 1) {
        fixedRate = 1
    }
    if (option && option['square']) {
        fixedRate *= fixedRate
    }
    return fixedRate * round * factor
}